.dm-job-year {
    min-height: 100vh;
    background-color: var(--greyWes);
    .tick {
        line {
            fill: none;
            stroke: var(--alternateWes);
            stroke-width: 1px;
            stroke-dasharray: 3px 3px;
            opacity: .7;
        }
        text {
            font-family: var(--roboto);
            font-size: 13px;
        }
    }

    .line {
        fill: none;
        stroke: var(--alternateWes);
    }
}
